import blogSummary from "../common/blogSummary";
import Paging from "../../../common/Paging";
import util from "../../../common/util";
import "../../scss/pages/essays-index.scss";
import Global from "../common/global";

(() => {
    let essayList = "/api/Essays/Index";
    let hotEssay = "/api/Essays/Hot";
    let latestEssay = "/api/Essays/Latest";
    let essayTypes = "/api/Taxonomy/EssayTypes";
    let tags = "/api/Taxonomy/Tags";

    let loadCount = 0;
    function hideLoading() {
        loadCount += 1;
        if (loadCount > 4) {
            Global.hideLoading();
        }
    }
    
    let paging = new Paging("#paginationContainer", (page) => {
        window.location.href = "/Essays?page=" + page;
    },true);

    function getEssaySummary(page: number = 1) {
        let data: any = {
            page: page
        };
        Global.showLoading();
        $.ajax({
            type: "get",
            url: essayList,
            data: data,
            success: (msg: any) => {
                var pagingData = msg.Data;
                blogSummary.create(pagingData.Data, '#bIContainer', true);
                paging.createPagingLi(pagingData.PageIndex, pagingData.TotalPages);
            },
            complete: () => {
                hideLoading();
            }
        });
    }

    function asideTool() {
        const getLatest = () => {
            $.ajax({
                url: latestEssay,
                type: "get",
                complete: () => {
                    hideLoading()
                },
                success: (msg: any) => {
                    if (msg.Code === 0) {
                        var data = msg.Data;
                        let html = data.map(
                            (item: any, index: number) => {
                                let className = 'rank';
                                if (index < 3) {
                                    className += " bg-red";
                                } else {
                                    className += " bg-blue";
                                }
                                return `<li><span class="${className}">${index + 1}</span><a href="/Essays/Details/${item.Slug}">${item.Title}</a></li>`;
                            }).join(" ");

                        $("#latest").html(`<ul>${html}</ul>`);
                    }
                }
            });
        }

        const getHot = () => {
            $.ajax({
                url: hotEssay,
                type: "get",
                complete: () => {
                    hideLoading()
                },
                success: (msg: any) => {
                    if (msg.Code === 0) {
                        var data = msg.Data;
                        let html = data.map((item: any, index: number) => {
                            let className = 'rank';
                            if (index < 3) {
                                className += " bg-red";
                            } else {
                                className += " bg-blue";
                            }

                            return `<li><span class="${className}">${item.ReadNum}</span><a href="/Essays/Details/${item.Slug}">${item.Title}</a></li>`;
                        }).join(" ");

                        $("#hot").html(`<ul>${html}</ul>`);
                    }
                }
            });
        }

        const getEssayTypes = () => {
            $.ajax({
                url: essayTypes,
                type: "get",
                complete: () => {
                    hideLoading()
                },
                success: (msg: any) => {
                    if (msg.Code === 0) {
                        var data = msg.Data;
                        let html = data.map((item: any) => {
                            return `<li><a href="/Taxonomy/EssayType?Id=${item.Id}">${item.Name}</a><span class="badge badge-info">${item.Num}</span></li>`;
                        }).join(" ");

                        $("#essayTypes").html(`<ul>${html}</ul>`);
                    }
                }
            });
        }

        const getTags = () => {
            $.ajax({
                url: tags,
                type: "get",
                complete: () => {
                    hideLoading()
                },
                success: (msg: any) => {
                    if (msg.Code === 0) {
                        var data = msg.Data;
                        let html = data.map((item: any) => {
                            return `<li><a class="btn btn-light btn-sm" href="/Taxonomy/Tag?Id=${item.Id}">${item.Name} <span class="badge badge-info">${item.Num}</span></a></li>`;
                        }).join(" ");

                        $("#tags").html(`<ul>${html}</ul>`);
                    }
                }
            });
        }

        getLatest();
        getHot();
        getEssayTypes();
        getTags();
    }
    let page = <string>util.pickUpUrlParams().get("page");
    if (!page) {
        getEssaySummary();
    } else {
        getEssaySummary(parseInt(page));
    }
    asideTool();
})();


